@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
    margin: 0;
    background-color: #1e1e1e;
    background-color: var(--background-color);
    overflow-x: hidden;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
  
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root{
    --primary-color: #fd5056;
    --primary-color-dark: #f7343a;
    --secondary-color: #f3f4f5;
    --secondary-color-dark: #11134f;
    --background-color: #1e1e1e;
    --background-color-dark: #131313;
    --primary-text-color: #f3f4f5;
    --secondary-text-color: #fd5056;
    --color-black: #000000;
}
.App{
  color: var(--primary-text-color);
  background-color: var(--background-color);
  width: 100%;
  min-height: 100vh;
}
.inline{
  display: inline;
}
.inline-block{
  display: inline-block;
}
.content-section{
  display: grid;
  grid-template-columns: 250px auto;
  min-height: -webkit-max-content;
  min-height: max-content;
}
.footer{
    display: none;
    position: fixed;
    bottom: 0px;
    width: 100%;
    height: 60px;
    background-color: var(--background-color);
}
.footer-items{
    display: flex;
    height: 100%;
    justify-content: space-around;
    align-items: center;
}
.footer-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}
.footer-item-icon{
    font-size: 1.8rem;
}
.footer-item-active{
    color: var(--primary-color);
    font-weight: 600;
}
.footer-item-text{
    margin-top: 3px;
}
@media only screen and (max-width: 800px){
    .footer{
        display: block;
    }
}
.navbar{
    font-size: 1.5rem;
}
.logo{
    margin: 0 2rem 0rem 1rem;
}
.logo-sub-text{
    font-size: inherit;
    color: var(--secondary-text-color);
}
.searchbar-container{
    flex: 2 1;
}
.btn-container-navbar{
    margin: 0 1rem;
}
/* Due to specificity issues i have to write this */
.input-box .navbar-search{
    width: 100%;
}

@media only screen and (max-width: 600px){
    .logo{
        margin: 0 1rem;
    }
}
.button-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 2px;
    font-size: 1.3rem;
}
.button-primary{
    background-color: var(--primary-color-dark);
    color: var(--primary-text-color);
}
.button-primary:hover{
    background-color: var(--primary-color);
    border: 2px solid var(--primary-color-dark);
}
.button-outline-secondary{
    background-color: transparent;
    border: 2px solid var(--primary-color);
}
.button-outline-secondary:hover{
    background-color: var(--primary-color);
}
.sidebar{
    grid-column-start: 1;
    grid-column-end: 2;
    background-color: var(--background-color);
    width: 250px;
}
.sidebar-items{
    margin-top: 1.5rem;
}
.sidebar-item{
    display: flex;
    align-items: center;
    width: 100%;
    padding: 1rem 0;
    margin: 0 7px;
}
.sidebar-item-active{
    border-bottom: 2px solid var(--primary-color);
    border-left: 2px solid var(--primary-color);
    border-top: 2px solid var(--primary-color);
    border-bottom-left-radius: 0.5rem;
    border-top-left-radius: 0.5rem;
}
.sidebar-icon{
    margin: 0 0.5rem 0 1rem;
}
.hero-section{
    width: 100%;
    height: 90vh;
    overflow: hidden;
}
.hero-image-container{
    width: 100%;
    height: 100%;
}
.hero-image{
    -webkit-filter: brightness(30%);
            filter: brightness(30%);
}
.hero-heading{
    font-size: 3rem;
    font-weight: 600;
}
.hero-button{
    margin-top: 1rem;
}
.hero-heading-sub-text{
    font-size: inherit;
    color: var(--primary-color);
}
.hero-text-overlay-container{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
@media only screen and (max-width: 1250px){
    .hero-text-overlay-container{
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-30%, -50%);
                transform: translate(-30%, -50%);
    }   
}
@media only screen and (max-width: 800px){
    .hero-text-overlay-container{
        /* display: flex;
        flex-direction: column;
        align-items: center;
        width: fit-content; */
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }   
}
.content{
    grid-column: 2 / 3;
}
@media only screen and (max-width: 800px){
    .content{ 
        grid-column: 1 / 3;
    } 
}
.videolisting-video-container{
    width: 20rem;
    margin: 0.5rem;
    border-radius: 0.1rem;
    cursor: pointer;
    background-color: var(--background-color-dark);
}
.videolisting-video{
    width: 100%;
    height: 100%;
}
.thumbnail-text-container{
    display: grid;
    grid-template-columns: 3.1rem auto;
    margin: 1rem 0.5rem;
}
.thumbnail-container{
    grid-column-start: 1;
    grid-column-end: 2;
    width: 2.8rem;
    height: 2.8rem;
}
.text-container{
    position: relative;
    grid-column-start: 2;
    grid-column-end: 3;
    margin: 0 1.5rem 0 0.3rem;
}
.video-title{
    width: 100%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    white-space: normal;
}
.video-sub-title{
    opacity: 0.8;
    margin-top: 0.3rem;
    font-size: 0.9rem;
}
.video-more-icon{
    position: absolute;
    top: 0.3rem;
    right: -20px;
    font-size: 1.5rem;
    cursor: pointer;
}
.video-modal{
    position: absolute;
    top: 1.9rem;
    right: -20px;
    width: -webkit-max-content;
    width: max-content;
    background-color: var(--background-color);
    z-index: 2;
    
}
.video-modal-item{
    display: flex;
    align-items: center;
    padding: 1rem 1rem;
    cursor: pointer;
    font-size: 1rem;
}
.video-modal-item:hover{
    background-color: var(--background-color-dark);
}
.video-modal-icon{
    margin-right: 10px;
    font-size: 1.2rem;
}
@media only screen and (max-width: 950px){
    .videolisting-video-container{
        width: 15rem;
        margin: 0.5rem;
    }
}
@media only screen and (max-width: 452px){
    .videolisting-video-container{
        width: 100%;
        margin: 1rem;
    }
}
.videos-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 1rem;
}

.authentication{
    display: grid;
    grid-template-columns: 1fr 1fr;
    border-radius: 8px;
    width: 90%;
    max-width: 93.7rem;
    margin: 0rem auto;
    padding: 5rem 1rem;
    box-shadow: var(--primary-color) 0px 3px 8px;
    grid-gap: 1rem;
    gap: 1rem;
    background-color: var(--background-color-dark);
}
.wrapper{
    position: relative;
    height: 100%;  
}
.blob{
    position: absolute;
    width: 30%;
    z-index: -1;
}
.blob1{
    top: 0px;
    left: -100px;
}
.blob2{
    bottom: 0px;
    right: 0px;
}
.remember-me-checkbox{
    margin-left: 4px;
}
.authentication-image-container{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column: 1 / 2;
    z-index: 2;
    width: 100%; 
}
.authentication-image-container img{
    width: 100%;
    max-width: 40rem;
}
.authentication-form-container{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column: 2 / 3;
    z-index: 2;
}
.authentication-form{
    background-color: var(--background-color);
    max-width: 31.2rem;
    width: 100%;
    border-radius: 6px;
    padding: 1rem 0.5rem;
    box-shadow: var(--primary-color) 0px 3px 6px, var(--primary-color) 0px 3px 6px;
}
.authentication-form-heading{
    text-align: center;
    margin: 0 1rem 0.3rem 1rem;
}
.authentication-form-sub-heading{
    margin: 0.3rem 1rem;
    text-align: center;
}
.form-input-box{
    position: relative;
    display: flex;
    justify-content: center;
    margin: 1.8rem 0rem;
}
.form-input-box input{
    width: 90%;
    padding-left: 1rem;
    font-size: 1rem;
    height: 3.12rem;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    border-radius: 3px;
    border: 2px solid #BCBCBC;
}
.form-input-box input:focus{
    outline: none;
    border: 2px solid var(--primary-color);
}
.form-input-box i{
    position: absolute;
    top: 0.68rem;
    right: 2.81rem;
    cursor: pointer;
    font-size: 1.5rem;
}
.mail-icon{
    position: absolute;
    top: 0.80rem;
    right: 2.81rem;
    cursor: pointer;
    font-size: 1.5rem;
    color: var(--color-black);
}
.checkbox-container{
    width: 90%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto 1.8rem auto;
}
.forgot-password-btn{
    color: var(--primary-text-color);
    background-color: transparent;
    cursor: pointer;
}
.forgot-password-btn:hover{
    color: var(--primary-color);
}
.form-cta-button{
    display: block;
    width: 90%;
    margin: 1rem auto;
    border-radius: 3px;
    text-align: center;
}
.login{
    padding: 0;
}
.forgot-password{
    padding: 0;
}
.logout-container{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 80vh;
}
.logout-btn-container{
    width: 100%;
    display: flex;
    justify-content: center;
}
@media only screen and (max-width: 1024px){
    .authentication{
        grid-template-columns: 100%;
        grid-template-rows: auto;
    }
    .authentication-image-container{
        grid-column: 1 / 1;
        grid-row: auto;
        z-index: 2;
        margin: 1rem auto;
    }
    .authentication-image-container img{
        max-width: 30rem;
        margin-right: 1.5rem;
    }
    .authentication-form-container{
        grid-column:1 / 1;
        grid-row: auto;
        z-index: 2;
        margin: 1rem;
    }
}
.toasts-container{
    z-index: 1000;
    position: fixed;
    top: 1rem;
    right: 10px;
}
.toast{ 
    min-width: 12.5rem;
    margin: 1rem 0;
}
.toast-text{
    flex: 1 1;
}
.toast-close-icon{
    cursor: pointer;
    font-size: 1.3rem;
    margin-left: 0.9rem;
}
.likedvideos-heading{
    font-size: 2rem;
    text-align: center;
    margin: 2rem 0 1rem 0 ;
}
.likedvideos-video-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 1rem;
}
.empty-list-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.empty-list-image-container{
    max-width: 28.1rem;
    margin: 1rem;
}
.history-videos-heading{
    font-size: 2rem;
    text-align: center;
    margin: 2rem 0 1rem 0 ;
}

.history-videos-video-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 1rem;
}
